import React from 'react';
import { Browser } from 'react-kawaii'
import './ErrorMob.css';

function ErrorMob(props) {
    return(
        <div className="ErrorMob">
            <Browser size={200} mood="ko" color="#61dafb" />
            <h5 style={{color:"white", textAlign:"center"}}>Приложение [пока] не поддерживает мобильное устройство.</h5>
            <h5 style={{color:"white", textAlign:"center"}}>Сорян.</h5>
        </div>
    );
}

export default ErrorMob;