import React, {useEffect, useRef} from 'react';
import './App.css';
import * as PIXI from 'pixi.js';
import {TweenLite} from 'gsap/TweenLite'
import {Modal, Button, Popover, OverlayTrigger} from 'react-bootstrap';

//import puppypawImg from './assets/jibcodi.png';
import normalMapImg from './assets/NormalMap.png';
import pawImg from './assets/paw.png';

function ErrorModal(props){

  const returnMain = () =>{
    window.location.search = "";
  }
  return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
          Ò̴̭͚̊̿ẃ̸̤̪̀Ó̸͓͠
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>Ошибка загрузки изображения. Возращение на главный экран</p>
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={returnMain}>Возращение</Button>
        </Modal.Footer>
      </Modal>
  );
}

function App(props) {

  const [modalShow, setModalShow] = React.useState(false);

  const returnMain = () =>{
    window.location.search = "";
  }
  
  const powerBoop = -20;
  const inputSizeAim = useRef(null);
  const valueToSmallAim = 15

  let sizeAim = 60;
  let smallSizeAim = sizeAim - valueToSmallAim;
  let bg;
  let rt; 
  let brush; 
  let aim;
  let brushFilter;
  let appbox;
  let gameCanvas;
  let trigerBoop = false;
  let enterImg = new Image();


  useEffect(()=>{
    const url = new URL(window.location.href);
    const puppypawImg = url.searchParams.get("img");
    enterImg.id = "loadingImg";
    enterImg.crossOrigin = "anonymous";
    enterImg.onload = () => {
      if(enterImg.width > enterImg.height){
        enterImg.setAttribute("style", "max-width: 100%; height: auto; width: auto/9;");
      }
      else{
        enterImg.setAttribute("style", "max-height: 100%; width: auto; height: auto/9;");
      }
      appbox = new PIXI.Application(enterImg.width, enterImg.height);
      rt = PIXI.RenderTexture.create(appbox.screen.width, appbox.screen.height);
      enterImg.remove();
      console.log(enterImg);
      appbox.loader.add('one', normalMapImg);
      appbox.loader.add('aim',pawImg);
      appbox.loader.load(appDidLoader);
      appbox.loader.onComplete.add(appReady);

    }
    enterImg.onerror = () =>{
      setModalShow(true);
    }
    enterImg.src = puppypawImg;
    gameCanvas.appendChild(enterImg);
    
  });

  const appReady = () => {
    gameCanvas.appendChild(appbox.view);
    document.getElementById("loader").remove();
    gameCanvas.onmouseover = () =>{
      gameCanvas.style.cursor = 'none';
    }
  }

  const appDidLoader = (loader, resources) => {
    let tempBg = new PIXI.Sprite(new PIXI.Texture(new PIXI.BaseTexture(enterImg)));
    tempBg.width = appbox.screen.width;
    tempBg.height = appbox.screen.height;
    tempBg.anchor.set(0.5);
    tempBg.x = appbox.screen.width / 2;
    tempBg.y = appbox.screen.height / 2;

    appbox.renderer.render(tempBg, rt)
    bg = new PIXI.Sprite(rt);
    appbox.stage.addChild(bg);
    brush = new PIXI.Sprite(resources.one.texture);
    
    appbox.stage.addChild(brush);
    brush.anchor.set(0.5)
    aim = new PIXI.Sprite(resources.aim.texture);
    aim.width = sizeAim;
    aim.height=sizeAim;
    aim.alpha= 0.7;
    aim.anchor.set(0.5);
    appbox.stage.addChild(aim);
    
    
    brushFilter = new PIXI.filters.DisplacementFilter(brush);
    brushFilter.scale.x = 0;
    brushFilter.scale.y = 0;
    brush.width = sizeAim;
    brush.height=sizeAim;
    bg.filters = [brushFilter]
    appbox.stage.interactive = true;
    appbox.stage
    .on('mousemove', onPointerMove)
    .on('pointerdown', onPointerDown)
    .on('pointerup', onPointerUp);
    
    appbox.start();
  }

  const onPointerDown = (event) => {
    trigerBoop = true;
    brush.position.copy(event.data.global);
    TweenLite.to(brushFilter.scale, 0.3, {x:powerBoop, y:powerBoop});
    TweenLite.to(aim, 0.3, {width: smallSizeAim, height: smallSizeAim, alpha: 0.1});
    // brushFilter.scale.x = powerBoop;
    // brushFilter.scale.y = powerBoop;

    // aim.width = aim.width - 15;
    // aim.height = aim.height - 15;
    // aim.alpha = 0.1;
  }

  const onPointerUp = (event) => {
    trigerBoop = false;
    TweenLite.to(brushFilter.scale, 0.5, {x:0, y:0});
    TweenLite.to(aim, 0.3, {width: sizeAim, height: sizeAim, alpha: 0.7});
    // brushFilter.scale.x = 0;
    // brushFilter.scale.y = 0;
    // aim.width = aim.width + 15;
    // aim.height = aim.height + 15;
    // aim.alpha = 0.7;
  }

  const onPointerMove = (event) => {
    aim.position.copy(event.data.global);
    if(trigerBoop)
        brush.position.copy(event.data.global);
  }

  const changeSizeAim=() => {
    
    sizeAim = inputSizeAim.current.value;
    smallSizeAim = sizeAim - valueToSmallAim;
    aim.width = sizeAim;
    aim.height =sizeAim;
    
    brush.width=sizeAim;
    brush.height=sizeAim;
    
  }

  const copyTextToClipboard = function(text) {
    var textArea = document.createElement("textarea");
  
    //
    // *** This styling is an extra step which is likely not required. ***
    //
    // Why is it here? To ensure:
    // 1. the element is able to have focus and selection.
    // 2. if element was to flash render it has minimal visual impact.
    // 3. less flakyness with selection and copying which **might** occur if
    //    the textarea element is not visible.
    //
    // The likelihood is the element won't even render, not even a
    // flash, so some of these are just precautions. However in
    // Internet Explorer the element is visible whilst the popup
    // box asking the user for permission for the web page to
    // copy to the clipboard.
    //
  
    // Place in top-left corner of screen regardless of scroll position.
    textArea.style.position = 'fixed';
    textArea.style.top = 0;
    textArea.style.left = 0;
  
    // Ensure it has a small width and height. Setting to 1px / 1em
    // doesn't work as this gives a negative w/h on some browsers.
    textArea.style.width = '2em';
    textArea.style.height = '2em';
  
    // We don't need padding, reducing the size if it does flash render.
    textArea.style.padding = 0;
  
    // Clean up any borders.
    textArea.style.border = 'none';
    textArea.style.outline = 'none';
    textArea.style.boxShadow = 'none';
  
    // Avoid flash of white box if rendered for any reason.
    textArea.style.background = 'transparent';
  
  
    textArea.value = text;
  
    document.body.appendChild(textArea);
    textArea.focus();
    textArea.select();
  
    try {
      var successful = document.execCommand('copy');
      var msg = successful ? 'successful' : 'unsuccessful';
      console.log('Copying text command was ' + msg);
    } catch (err) {
      console.log('Oops, unable to copy');
    }
  
    document.body.removeChild(textArea);
  }
  
  const popover = (
    <Popover id="popover-basic">
      <Popover.Content>
        Ссылка скопирована
      </Popover.Content>
    </Popover>
  );


  return (
    <div className="App">
      <div id="rangeblock">
        <label className="labelrange" for="size"><h5>Размер прицела :3</h5></label> 
        <input onInput={changeSizeAim} ref={inputSizeAim} type="range" id="size" name="size" min="60" max="90"/>

        <div style={{height:"20px"}}></div>
        
        <div style={{width:"250px",display:"flex"}}>
          <Button 
          variant="primary"
          onClick={()=>{window.location.search = ""}}>
            Изменить
          </Button>

          <div style={{margin:"15px"}}></div>

          <OverlayTrigger trigger="click" placement="top" overlay={popover}>
            <Button 
            variant="primary"
            onClick={()=>{copyTextToClipboard(window.location.href)}}>
              Поделиться
            </Button>
          </OverlayTrigger>
        </div>

      </div>
      <div
      id="boopBlock" 
      ref={(monitorRef) => {gameCanvas = monitorRef}} 
      >
        <div id="loader"></div>
      </div>
      <ErrorModal
       show={modalShow}
       onHide={returnMain}
      />
    </div>
  );
}

export default App;
