import React, {useEffect, useState} from 'react';
import {Button, InputGroup, Form, Modal} from 'react-bootstrap';
import './Main.css';

function ErrorModal(props){
    return (
        <Modal
          {...props}
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
            ÒwÓ
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
          <h4>Упсс изображение не загрузилось</h4>
            <p>
                Это часто происходит по <b> трём причинам </b>.
            </p>
            <h4>Первая причина</h4>
            <p>
                <b>Не правильно введена ссылка на изображение. </b> Проверти ввод и попробуйте снова.
            </p>
            <h4>Вторая причина</h4>
            <p>
                Рессур на котором храниться изображение немного <b> тормозит или не исправен </b>. Попробуйте открыть изображение несколько раз.
                Если ничего не изменилось и вы видите это окно не первый раз, то скорее всего ваше изображение не загружается по причине 3.
            </p>
            <h4>Третья причина</h4>
            <p>
                Ресурc на котором храниться изображение <b>запрещает сторонним ресурсам получать данные изображения</b>. Этот нельзя исправить.
                Если вы хотите использовать именно это изображение попробуйте загрузить его на рекомендуемый ресурс(смотри ниже) и вставьте новую ссылку.
            </p>
            <h4>Рекомендуемые ресурсы</h4>
            <p>
                Рекомендуемые ресурсы: <b> <a href="https://vk.com">VK</a>, <a href="https://twitter.com">Twitter</a>,
                 <a href="https://imgur.com/">Imgur</a></b>.
            </p>
            <p>
                Эти ресурсы предоставляют сторонним ресурсам брать данные изображений с их сервера. 
                Если у вас возникла ошибка с изображениями с этих ресурсов, то скорее всего это связано с причиной 1 или 2, 
                либо конкретно это изображение ограничено в доступности и защищено внутренней  системой. 
            </p>
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={props.onHide}>Закрыть</Button>
          </Modal.Footer>
        </Modal>
    );
}

function InfoModal(props){
    return (
        <Modal
          {...props}
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
            OWO
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
          <h4>Привет</h4>
            <p>
                Это приложение было сделано на коленке, ради забавы и т.д. и т.п.
            </p>
            <p>
                В этом приложении вы можете взять изображения с интернета и "потыкать" в него. Очень хорошо тыкается в выпуклые выпуклости и в большие лапки :3
            </p>
            <p>
                Рекомендую брать картинки с ресурсов: <b> <a href="https://vk.com">VK</a>, <a href="https://twitter.com">Twitter</a>,
                 <a href="https://imgur.com/"> Imgur</a></b>. Так как они не запрещают брать данные изображений сторонним ресурсам (как этот)
            </p>
            <p> 
                Развлекайтесь %3 
            </p>


          </Modal.Body>
          <Modal.Footer>
            <Button onClick={props.onHide}>Закрыть</Button>
          </Modal.Footer>
        </Modal>
    );
}



function Main(props) {
    const [errorModalShow, setErrorModalShow] = useState(props.error);
    const [infoModalShow, setInfoModalShow] = useState(false);

    let inputUrlImage;
    useEffect(()=>{
        inputUrlImage = document.getElementById("urlimg");
    });

    const uwuImage = () => {
        const params = new URLSearchParams(window.location.search);
        const urlImg = inputUrlImage.value;
        let enterImg = new Image();

        enterImg.id = "loadingImg";
        enterImg.crossOrigin = "anonymous";
        enterImg.src = urlImg;
        enterImg.onerror = () =>{
            setErrorModalShow(true);
        }
        enterImg.onload = ()=>{
            params.set('img', urlImg);
            console.log(params.toString());
            window.location.search = params;
        }
    }

    return(
        <div className="Main">
            <div className="label">
                <div>Paw</div> 
                <div>Paw</div>
            </div>
            <div className="urlin">
                <InputGroup className="mb-3">
                    <Form.Control
                    id="urlimg"
                    type="text"
                    as='input'
                    placeholder="Вставте URL картиники чтобы тыкать"
                    />
                    <InputGroup.Append>
                    <Button variant="primary" onClick={uwuImage}>UWU</Button>
                    <div style={{width:"5px"}}></div>
                    <Button variant="primary" onClick={()=>setInfoModalShow(true)}>Инфо</Button>
                    </InputGroup.Append>
                </InputGroup>
            </div>
            <ErrorModal
                show={errorModalShow}
                onHide={() => setErrorModalShow(false)}
            />
            <InfoModal
                show={infoModalShow}
                onHide={() => setInfoModalShow(false)}
            />
        </div>
    );
}

export default Main;